body{
  background: #fff;
}
#root{
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.App{
    border: 1px solid #ccc;
    background: #fff;
    margin: 2em;
    width: 100%;
    max-width: 32em;
    min-height: 20em;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
    position: relative;
}
.App h1{
    margin: 0 0 16px;
    padding: 0.4em 0;
    text-align: center;
    color: #fff;
    background: #3F80E1;
}
.App h1 button{
    font-size: 16px;
    position: absolute;
    right: .4em;
    top: .4em;
    color:#fff ;
    background: #3F80E1;
    border: none;
}
.App button:focus{
    outline: none;
}
.inputWrapper input{
    margin-left: 10%;
    margin-right: 10%;
    padding: 10px;
    border: 1px solid white;
    box-shadow: #ccc;
}
.App input:focus{
    outline: none;
}
.App input::-webkit-input-placeholder{
    color: #ccc;
}
.App input::-ms-input-placeholder{
    color: #ccc;
}
.App input::placeholder{
    color: #ccc;
}
.itemWrapper{
    width: 80%;
    margin: 10px auto;
}
.itemWrapper::after{
    content: '';
    display: block;
    clear: both;
}
.itemWrapper input{
    width: 14px;
    height: 14px;
    margin-left: 12px;
    margin-right: 12px;
    margin-top: 5px;
    float: left;
}
.itemWrapper span{
    width: 60%;
    line-height:22px;
    float: left;
    word-break: break-all;
}
.itemWrapper button{
    display: none;
    float: right;
    background:#fff;
    color: #ccc;
    border: 0;
}
.itemWrapper:hover button{
    display: inline-block;
    color: #3F80E1;
}
.itemWrapper button:focus{
    outline: none;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::after,*::before{
    box-sizing: border-box;
}
ul,ol,dl{
    list-style: none;
}
a{
    color:inherit;
}
.clearfix::after{
    content: '';
    display: block;
    clear: both;
}
.UserDialogWrapper{
    background:#EDEDED;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.UserDialog{
    border-radius: 4px;
    width: 100%;
    max-width: 20em;
    background: white;
    box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
}
.UserDialog > .formWrapper{
    padding: 0 1em 1em;
}
.UserDialog  form >.row{
    padding: .3em 0;
}
.UserDialog  form >.row.actions{
    width: 80%;
    margin: 6px auto;
    line-height: 1.8em;
}
.UserDialog  form >.row.actions button{
    color:#fff;
    width: 100%;
    margin: 0 auto;
    line-height: 1.8em;
    border: 1px solid transparent;
    border-radius: 4px;
    background: #3F80E1;
}
.UserDialog  form >.row.actions .forgetPasswordWrapper{
    width: 100%;
}
.UserDialog  form >.row.actions a{
    margin: 6px 0 ;
    float: right;
    color: #3F80E1;
    text-decoration: none;
}
.UserDialog  form >.row.actions button:focus{
    outline: none;
}
.UserDialog  form .row input{
    display: block;
    width: 80%;
    margin: 0 auto;
    line-height: 1.8em;
    padding-left:0.4em ;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom:2px solid #ccc; 
}
.UserDialog  form .row input:focus{
    border-bottom-color:#3F80E1;
}
.UserDialog footer{
    display: block;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 16px;
}
.UserDialog footer button{
    color: #3F80E1;
    background: #fff;
    border:none;
}
.UserDialog footer button:focus{
    outline: none;
}
.UserDialog footer button:first-child{
    float: left;
}
.UserDialog footer button:last-child{
    float: right;
}
